.profile{
  &-info{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__img{
      width: 75px;
      height: 75px;
      flex-shrink: 0;

      border-radius: 50%;
      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__content{
      margin-left: 15px;
    }
    &__text{
      font-weight: 600;
      font-size: 21px;
      line-height: 26px;

      letter-spacing: -0.2px;

      color:$monoBlack;

      max-width: 280px;

      word-break: break-word;
    }
    &__balls{
      margin-left: 40px;
    }
    &__desc{
      margin-top: 20px;

      font-size: 16px;
      font-weight: 400;

      color:$secondary;

      max-width: 600px;
    }
  }
  &-content{
    &__block{
      & + &{
        margin-top: 30px;
      }
    }
    &__box{
      margin-top: 15px;
    }
    &__text{
      font-family: 'Nunito Sans', sans-serif;
      font-size: 18px;
      line-height: 24px;

      color:$secondary;
      &-link{
        color:$accent;
        transition: .5s color;
        &:hover{
          color:$accentHover;
        }
      }
      &-small{
        max-width: 435px;
      }
    }
    &__title{
      color:#020203;

      font-family: 'Inter', sans-serif;
      font-size: 17px;
      line-height: 21px;
      font-weight: 600;
    }
    &__desc{
      font-size: 16px;
      color:#95929C;
    }
    &__input{
      max-width: 470px;
      &-item{
        background: #FFFFFF;
        border: 1px solid $lightGrey;
        border-radius: 5px;

        height: 60px;
      }
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(auto-fit,minmax(0,370px));
      gap: 30px;
    }
    &__gender{
      display: flex;
      align-items: center;
      &-block{
        & + &{
          margin: 0 0 0 15px;
        }
      }
    }
    &__btn{
      max-width: 270px;
      height: 50px;
    }
  }
}

.balls{
  &-box{
    &__wrapper{
      padding: 20px;

      background: #FFFFFF;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
      border-radius: 15px;

      width: 210px;
    }
    &__title{
      font-size: 16px;
      font-weight: 500;
      color:$secondary
    }
    &__count{
      display: flex;
      justify-content: center;
      align-items: center;

      width: 100%;
      background: $accentSecondary;
      padding: 8px 16px;
      border-radius: 21px;

      margin-top: 10px;

      font-size: 16px;
      line-height: 1;
      font-weight: 500;
      color:$accent;
      &::before{
        content: '';

        width: 24px;
        height: 24px;
        display: block;

        margin-right: 10px;

        background: url("../img/coins.png") center center/contain no-repeat;
      }
      span{
        font-size: 21px;
        font-weight: 600;
        letter-spacing: -0.2px;

        display: block;

        margin-right: 3px;
      }
    }
  }
}

.change{
  &-avatar{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__img{
      width: 75px;
      height: 75px;

      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;

        border-radius: 50%;
      }
    }
    &__btn{
      margin: 0 0 0 20px;

      max-width: 175px;
      height: 50px;
    }
  }
}

@media screen and (max-width: 700px) {
  .profile{
    &-info{
      &__wrapper{
        flex-wrap: wrap;
      }
      &__balls{
        margin: 15px 0 0 0;
      }
      &__content{
        flex-basis: calc(100% - 90px);
      }
      &__desc{
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .profile{
    &-content{
      &__text{
        &_center{
          &-mobile{
            text-align: center;
          }
        }
      }
      &__btn{
        max-width: 100%;
      }
    }
  }
}