.footer{
  position: relative;
  z-index: 8;

  background: #fff;
  &__wrapper{
    background: #fff;

    padding: 25px 0 20px 0;
  }
  &__row{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    & + &{
      margin-top: 32px;
    }
  }
  &__logo{
    height: 60px;
    max-width: 228px;
    width: 100%;
    &-text{
      font-family: 'Montserrat', sans-serif;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;

      color: #3A4972;
    }
  }
  &__copyright{
    font-family: 'Nunito Sans', sans-serif;
    color:$monoBlack;
    font-size: 16px;
  }
  &__link{
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 400;
    font-size: 16px;

    color:$accent;

    text-align: right;

    transition: .5s color;
    &:hover{
      color:$accentHover;
    }
  }
  &-social{
    &__text{
      color:#000;
      font-size: 18px;
    }
    &__list{
      margin-top: 15px;

      display: flex;
      align-items: center;
      justify-content: flex-end;
      &-item + &-item{
        margin-left: 12px;
      }
      &-link{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: #BABABA;

        transition: .5s background-color;
        &:hover{
          background-color: #CBCBCB;
        }
      }
      &-icon{
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .footer{
    &-social{
      &__text{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 767px) {
  .footer{
    &__row{
      align-items: center;
    }
    &__logo{
      &-text{
        text-align: center;
      }
    }
    &-social{
      &__list{
        margin-top: 0;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .footer{
    &__logo{
      height: 40px;
    }
    &__wrapper{
      padding: 25px 0 35px 0;
    }
    &__row{
      flex-direction: column;
      & + &{
        margin-top: 20px;
      }
    }
    &__block{
      & + &{
        margin-top: 20px;
      }
    }
  }
}