body {
  font-family: 'Montserrat', sans-serif;

  font-size: 18px;
  line-height: 136%;

  color: #000;

  font-weight: 400;
}

.container {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;

  margin: 0 auto;
}

.page {
  &-wrapper {
    display: flex;
    flex-direction: column;

    min-height: 100vh;

    &__header, &__footer {
      flex: 0 0 auto;

      background-color: #fff;
    }

    &__content {
      flex: 1 0 auto;

      padding: 50px 0;

      background-color: #F8FAFF;

      &_big{
        padding: 80px 0;
      }
      &_banner{
        padding: 160px 0 115px 0;
      }
    }
  }

  &-content {
    &__header {
      position: relative;
      &-block {
        & + & {
          margin-top: 40px;
        }
      }

      &-back {
        position: absolute;
        left: 0;

        top: -40px;
      }

      &-back + &-block{
        padding-top: 40px;
      }

    }

    &__title {
      font-size: 30px;
      font-weight: 700;
      line-height: 36px;

      color: $monoBlack;
    }

    &__subtitle{
      margin: 15px 0 0 0;

      font-family: 'Montserrat', sans-serif;
      font-weight: 600;
      font-size: 21px;
      line-height: 26px;
      letter-spacing: -0.2px;

      color: #5C82D7;
    }

    &__desc{
      margin-top: 40px;
    }

    &__header + &__wrapper{
      padding-top: 40px;
    }
  }

  &-tabs {
    &__nav {
      display: flex;
      align-items: center;

      &-item {
        width: 100%;
        max-width: 150px;
        height: 60px;

        color: $secondary;

        border-bottom: 2px solid transparent;

        transition: .5s border-bottom-color;

        &.active {
          color: $accent;

          border-bottom-color: $accent;

          &:hover {
            color: $accentHover;

            border-bottom-color: $accentHover;
          }
        }
      }

      &-link {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 100%;

        font-size: 17px;
        font-weight: 600;
        color: inherit;

        transition: .5s color;

        outline: none;
      }
    }

    &__content {
      padding-top: 60px;
      &-small{
        padding-top: 40px;
      }
    }
  }
}

.icon {
  width: 100%;
  height: 100%;
}

.input {
  &.error{
    .input__item{
      border-color: #DD4534;
    }
    .input__text{
      color:#DD4534;
    }
  }
  &__text{
    margin: 10px 0 0 0;

    font-family: 'Nunito Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;

    color: #898D93;

  }
  &__item {
    -webkit-appearance: none;
    border: none;
    background: none;
    outline: none;

    width: 100%;
    height: 100%;

    font-family: 'Nunito Sans', sans-serif;
    padding: 0 16px;
    font-size: 16px;
    color: $monoBlack;
  }
  &-block{
    position: relative;
  }
  &-password{
    position: relative;
    &__icon{
      position: absolute;
      right: 16px;
      top:calc(50% - 12px);
      width: 24px;
      height: 24px;
      display: block;

      background: url("../img/eye.svg") center center/contain no-repeat;
      &.active{
        background: url("../img/eye-close.svg") center center/contain no-repeat;

      }
    }
  }
}


.tabs {
  &-content {
    &__item {
      display: none;

      &.active {
        display: block;
      }
    }
  }
}

.btn {
  -webkit-appearance: none;

  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  font-family: 'Nunito Sans', sans-serif;

  &-accent {
    background-color: $accent;

    font-family: 'Nunito Sans', sans-serif;
    font-size: 16px;
    font-weight: 600;
    color: #fff;

    border-radius: 4px;

    transition: .5s background-color;

    &:hover {
      background-color: $accentHover;
    }

    &-light {
      background-color: $accentSecondary;

      font-size: 16px;
      font-weight: 600;
      color: $accent;

      border-radius: 4px;

      transition: .5s background-color;

      &:hover{
        background: #DBE6FD;
      }
    }

  }

  &-back{
    &::before{
      content: '';
      width: 24px;
      height: 24px;
      display: block;

      background: url("../img/arrow-back.svg") center center/contain no-repeat;

      margin: 0 8px 0 0;
    }
  }

}

.back-link {
  width: 115px;
  height: 40px;
}


.link {
  color: $accent;

  transition: .5s color;

  &:hover {
    color: $accentHover;
  }
}

.delete-list{
  li + li{
    margin-top: 15px;
  }
  li a{
    text-decoration: underline;
  }
}

.progress{
  &-bar{
    width: 100%;
    height: 4px;

    background: $lightGrey;

    border-radius: 2px;

    position: relative;
    &_big{
      height: 10px;
      border-radius: 0;
    }
    &-current{
      position: absolute;
      left:0;
      bottom: 0;
      width: 0;

      height: 100%;

      background: $accent;

      border-radius: 2px;
    }
  }
}

.trigger{
  &-link{
    &:hover{
      cursor: pointer;
    }
  }
  &-block{
    display: none;
    &.active{
      display: block;
      animation: showHide 0.5s forwards;
    }
  }
}

.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  &.active{
    z-index: 9;
    display: block;
  }
  &__row{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    &-block{
      max-width: 200px;
    }
  }
  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    background: rgba(0, 0, 0, 0.2);

    max-height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;

    padding: 60px 25px;
  }
  &__content{
    max-width: 460px;
    width: 100%;

    padding: 60px 40px;

    background: #FFFFFF;
    border-radius: 20px;

    position: relative;
    &-small{
      padding: 60px 40px 25px 40px;
    }
  }
  &__close{
    position: absolute;
    top: 40px;
    right: 40px;

    width: 20px;
    height: 20px;

    &:hover{
      cursor: pointer;
      .popup__close-img{
        filter: brightness(.7);
      }
    }
    &-small{
      top: 20px;
      right: 20px;
    }
    &-img{
      width: 100%;
      height: 100%;

      object-fit: contain;

      transition: .5s all linear;
    }
  }
  &__social{
    display: grid;
    grid-template-columns: repeat(5,minmax(0,1fr));
    gap: 30px;
  }
  &__btn{
    height: 60px;
  }
  &__title{
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;

    color: #020203;
  }
  &__text{
    font-family: 'Nunito Sans',sans-serif;
    font-size: 16px;
    line-height: 22px;

    color:$monoBlack;
    & + &{
      margin-top: 5px;
    }
    &-bold{
      font-weight: 600;
    }
    &-accent{
      color:$accent;
    }
    &-grey{
      color: #95929C;
    }
    &-small{
      font-size: 16px;
    }
    &-big{
      font-size: 21px;
      line-height: 25px;
    }
    &-center{
      text-align: center;
    }
  }
  &__link{
    margin: 0 0 10px 0;
  }
  &__block{
    & + &{
      margin-top: 24px;
    }
    & + &-small{
      margin-top: 20px;
    }
  }
  &__block + &__footer{
    margin-top: 60px;
  }
  &__input{
    border-color: #D2D2D2;

    color:#020203;

    position: relative;
    &.error{
      border-color: #FF7C7C;

      color:#FF7C7C;
    }
    &.succes{
      border-color: $monoBlack;

      color:$monoBlack;
    }
    &-item{
      -webkit-appearance: none;
      width: 100%;
      height: 60px;

      background: #FFFFFF;
      border: 1px solid;
      border-radius: 5px;
      padding: 15px;

      border-color: inherit;

      font-size: 18px;
      color:#020203;
      outline: none;

      transition: .5s all;
      &::placeholder{
        color: #95929C;
      }
      &:focus{
        border-color: #95929C;
      }
    }
    &-text{
      font-family: 'Nunito Sans', sans-serif;
      font-size: 15px;
      line-height: 20px;

      color: inherit;
      left: 0;

      margin-top: 10px;
    }
  }
}

.custom-select{
  &-opener{
    padding: 8px 16px;

    width: 226px;
    height: 45px;

    border: 1px solid #D2D2D2;
    border-radius: 5px;

    background: none;

    font-size: 16px;
    font-weight: 500;
    color: #020203;

    display: flex;
    align-items: center;
    &::after{
      content: '';

      display: block;
      width: 12px;
      height: 6px;

      margin-left: 10px;

      background: url("../img/select-arrow.svg") center center/contain no-repeat;
    }
    span{
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
  &-panel{
    max-width: 100%;
    width: max-content;

    top: calc(100% + 13px);

    z-index: 10;


    background: #FFFFFF;
    box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
    border-radius: 5px;

    padding: 10px;
    display: none;
  }
  &-container{
    &.is-open{
      .custom-select-panel{
        display: block;
      }
    }
  }
  &-option{
    border-radius: 5px;
    padding: 10px 15px;
    &.is-selected{
      background: rgba(235, 235, 235, 0.5);
      transition: .4s background-color linear;
      &::before{
        display: none;
      }
    }
    &.has-focus{
      background:rgba(235, 235, 235, 0.2);
    }
  }
  &-option + &-option{
    margin-top: 5px;
  }
}

.star-block{
  display: grid;
  grid-template-columns: repeat(5,minmax(0,20px));
  grid-auto-rows: 20px;
  gap: 10px;
  &__input{
    display: none;
  }
  &__item{
    svg{
      path{
        transition: .5s all;
      }
    }
    &.active{
      svg{
        path{
          fill:#FDCC0D;
          stroke:#FDCC0D;
        }
      }
    }
  }
}

@media screen and (max-width: 1220px) and (min-width: 769px)  {
  .container{
    padding: 0 95px;
  }
}

@media screen and (max-width: 500px) {
  .page {
    &-tabs {
      &__nav {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
      }
    }
  }
}


@keyframes showHide {
  0%{
    display: none;
    opacity: 0;
  }
  50%{
    display: block;
  }
  100%{
    opacity: 1;
  }
}


@media screen and (max-width: 1200px) {
  .page{
    &-wrapper{
      &__content{
        &_big{
          padding: 40px 0;
        }
      }
    }
    &-content{
      &__header{
        &-back{
          position: relative;
          top: unset;
          left: unset;
        }
        &-back + &-block{
          margin-top: 30px;
        }
      }
    }
  }
}


@media screen and (max-width: 991px) {
  .page{
    &-tabs{
      &__content{
        padding-top: 40px;
      }
    }
    &-wrapper{
      &__content{
        &_big{
          padding: 40px 0;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .popup__close{
    top: 14px;
    right: 20px;
  }
}


@media screen and (max-width: 400px) {
  .popup{
    &__wrapper{
      align-items: flex-start;

      padding-top: 120px;
    }
    &__content{
      padding: 60px 20px;
    }
    &__title{
      text-align: center;
    }
    &__btn{
      text-align: left;
    }
  }
}