.promocode{
  &-grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 150px;
    gap:30px;
    &_small{
      grid-auto-rows: 110px;
    }
  }
  &-item{
    height: 100%;
    display: block;
    &__wrapper{
      height: 100%;

      display: flex;
      flex-direction: column;
      justify-content: space-between;

      padding: 20px;
      border-radius: 25px;
      background: #fff;

      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);

      transition: .5s all linear;
      &:hover{
        box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);

        .promocode-item__footer{
          opacity: 1;
        }
      }
    }
    &__header + &__content{
      margin-top: 15px;
    }
    &__balls{
      font-size: 16px;
      color:$accent;
    }
    &__title{
      color:$monoBlack;
      font-size: 21px;
      font-weight: 600;
      letter-spacing: -0.2px;

      word-break: break-all;
    }
    &__footer{
      margin-top: auto;

      opacity: 0;

      transition: .5s opacity linear;
    }
    &__link{
      font-size: 17px;
      font-weight: 600;
      color:$accent;
    }
  }
  &-module{
    &__header + &__content{
      margin-top: 40px;
    }
    &__header{
      display: flex;
      align-items: center;
      &::before{
        content: '';
        width: 36px;
        height: 36px;
        display: block;
        background: url("../img/module-arrow.svg") center center/contain no-repeat;
        margin-right: 20px;
        transform: rotate(-90deg);
        transition: .3s all;
      }
      &.active{
        &::before{
          transform: rotate(0);
        }
      }
    }
    &__title{
      font-weight: 600;
      font-size: 21px;
      letter-spacing: -0.2px;
      color:$monoBlack;
    }
  }
}
.promocodes{
  &-wrapper{
    &__item + &__item{
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 991px) {
  .promocode{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,270px));
    }
  }
}

@media screen and (max-width: 600px) {
  .promocode{
    &-grid{
      grid-template-columns: minmax(0,270px);
      gap: 15px;
    }
  }
}