.courses{
  &-empty{
    &__img{
      width: 100px;
      height: 80px;

      margin: 0 auto;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__content{
      max-width: 492px;
    }
    &__text{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 136%;

      color: $secondary;
    }
    &__footer{
      margin-top: 30px;
    }
    &__btn{
      max-width: 380px;
      height: 60px;
    }
  }
  &-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(0,370px));
    gap:30px;

    padding-bottom: 30px;
  }
  &-card{
    &__wrapper{
      position: relative;

      min-height: 312px;

      background: #FFFFFF;
      border: 1px solid rgba(188, 197, 218, 0.5);
      border-radius: 15px;

      overflow: hidden;

      display: flex;
    }
    &__img{
      position: absolute;
      z-index: 2;
      left: 0;
      top: 0;

      width: 100%;
      height: 100%;

      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__content{
      position: relative;
      z-index: 3;

      padding: 20px;

      display: flex;
      flex-direction: column;

    }
    &__info{
      display: flex;
      align-items: center;
      &-item{
        display: flex;
        align-items: center;
        & + &{
          margin: 0 0 0 15px;
        }
      }
      &-icon{
        width: 24px;
        height: 24px;

        flex-shrink: 0;
      }
      &-text{
        margin: 0 0 0 5px;

        font-family: 'Nunito Sans', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 136%;

        color: $accent;
      }
    }
    &__box{
      margin: 15px 0 0 0;
    }
    &__title{
      font-family: 'Montserrat', sans-serif;
      font-weight: 600;
      font-size: 21px;
      line-height: 26px;
      letter-spacing: -0.2px;

      color: $monoBlack;
    }
    &__text{
      margin: 15px 0 0 0;


      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;

      color: $monoBlack;
    }
    &__footer{
      margin: auto 0 0 0;

      padding: 30px 0 0 0;
    }
    &__btn{
      max-width: 270px;
      height: 50px;
    }
  }
  &-search{
    &-failed{
      display: flex;
      align-items: center;
      justify-content: center;

      padding-top: 60px;
      &__wrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        max-width: 370px;
        width: 100%;
      }
      &__img{
        width: 100px;
        height: 100px;

        object-fit: contain;
      }
      &__text{
        margin-top: 50px;

        text-align: center;

        font-family: 'Nunito Sans', sans-serif;
        font-size: 18px;
        color: $monoBlack;
      }
    }
  }
}


@media screen and (max-width: 1200px) {
  .courses{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
    }
  }
}

@media screen and (max-width: 800px) {
  .courses{
    &-grid {
      grid-template-columns: minmax(0, 1fr);
      grid-auto-rows: auto;
      gap: 25px;
    }
  }
}
