.box-404{
  display: flex;
  align-items: center;
  justify-content: center;
  &__wrapper{
    max-width: 465px;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__title,&__desc{
    line-height: 1;
    color:$monoBlack;
    text-align: center;
  }
  &__desc{
    font-family: 'Nunito Sans', sans-serif;
    line-height: 136%;
  }
  &__title{
    font-size: 90px;
    font-weight: 600;

    color:$accent;
  }
  &__desc{
    font-size: 21px;
    margin-top: 30px;
  }
  &__btn{
    margin-top: 35px;

    height: 60px;
    max-width: 380px;
  }
}

@media screen and (max-width: 600px) {
  .box{
    &-404{
      &__desc{
        font-size: 18px;
      }
    }
  }
}