.tabs-nav {
  z-index: 1;
  position: relative;
}
.no_group_header {
  position: relative;
}
.filter {
  margin-bottom: -40px;
  margin-top: 40px;
}
.filter-choise {
  margin-top: 30px;
  margin-bottom: 20px;
}
.course-module__title span {
  margin-left: 0;
}
.container_h {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;
  margin: 0 auto;

}
.lesson-item__wrapper {
  width: 100%;
}
@media screen and (max-width: 1100px) and (min-width: 992px) {
  .header-list__link {
    font-size: 17px;
  }
}
@media screen and (max-width: 767px) {
  .course-module__title span {
    position: relative;
    margin-bottom: -30px;
  }
}
@media screen and (max-width: 650px) {
  .filter {
    margin-bottom: -10px;
  }
}

.popup__close-img.replaced-svg:hover {
  cursor: pointer;
}
.popup__close-img.replaced-svg:hover path{
  fill: #0a0a0a;
}
@media screen and (min-width: 601px) {
  .page-nav__item.page-nav__item_mobile {
    display: none;
  }
}

[v-cloak] {
  display: none;
}

.header_with-back {
  border-bottom: 1px solid white;
}
#core_back {
  padding-top: 80px;
  padding-bottom: 20px;
  z-index: 9;
  position: absolute;
  width: 100%;
}
@media screen and (max-width: 1200px) {
  #core_back {
    padding-top: 40px;
  }
}
.notification-block .close {
  position: absolute;
  top: 0;
  right: 10px;
  background: transparent;
  font-size: 30px;
}