.page {
  &-content{
    &__search{
      -webkit-appearance: none;
      border: none;
      outline: none;
      background: rgba(137, 141, 147, 0.15);
      border-radius: 10px;

      width: 100%;
      height: 60px;

      padding: 0 32px;

      font-size: 18px;
      &::placeholder{
        color:rgba(#020203,.5);
      }
    }
  }
  &-blog {
    &__grid {
      &-row{
        display: grid;
        grid-template-columns: 770px auto;
        gap: 30px;
        & + &{
          margin-top: 40px;
        }
      }
    }
    &__filter{
      max-width: 670px;
    }
  }
}

.blog{
  &-grid{
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));

      gap:40px 30px;
    }
  }
  &-item{
    &__header{
      height: 216px;
      position: relative;

      padding: 20px;

      border-radius: 15px;
      overflow: hidden;
    }
    &__img{
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;

        border-radius: 15px;
      }
    }
    &__title{
      position: relative;
      z-index: 2;

      font-family: 'Montserrat', sans-serif;
      font-weight: 600;
      font-size: 26px;
      line-height: 32px;
      letter-spacing: -0.2px;

      color: $monoBlack;
    }
    &__content{
      margin: 15px 0 0 0;
    }
    &__category{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;

      color: $accent;
    }
    &__text{
      margin: 10px 0 0 0;

      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;
      color: $monoBlack;
    }
  }
  &-article{
    &__wrapper{
      max-width: 670px;
      width: 100%;
    }
    &__title{
      font-family: 'Montserrat', sans-serif;
      font-weight: 700;
      font-size: 30px;
      line-height: 44px;
      color: #020203;
    }
    &__tag{
      margin-top: 30px;
    }
    &__img{
      margin-top: 35px;

      width: 100%;
      height: 230px;
      border-radius: 10px 10px 0px 0px;
      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
        object-position: center center;
      }
    }
    &__content{
      margin-top: 40px;
    }
    &__description{
      font-family: 'Inter', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;

      color: #000000;
      p + p{
        margin-top: 30px;
      }
      p + blockquote{
        margin-top: 35px;
      }
      blockquote + p{
        margin-top: 35px;
      }
      h4 + p{
        margin-top: 30px;
      }
      p + h4{
        margin-top: 30px;
      }
      h4 + blockquote{
        margin-top: 35px;
      }
      blockquote + h4{
        margin-top: 35px;
      }

      img{
        margin: 30px 0;

        width: 100%;
        height: 100%;

        object-fit: cover;

        border-radius: 15px;
      }

      blockquote{
        padding: 15px 20px 15px 20px;
        position: relative;

        display: flex;
        align-items: center;

        background: $accentSecondary;
        &::before{
          content: '';
          position: absolute;
          left: 0;
          top: 0;

          height:100%;
          width: 3px;
          display: block;
          background: $accent;
        }
      }
      h4{
        font-family: 'Nunito Sans', sans-serif;
        font-weight: 700;
        font-size: 26px;
        line-height: 35px;
        letter-spacing: -0.2px;

        color: $accent;
      }
    }
  }
}


@media screen and (max-width: 1100px) {
  .page{
    &-blog{
      &__grid{
        &-row{
          grid-template-columns: minmax(0,1fr);
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .blog{
    &-grid{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
      }
    }
    &-article{
      &__img{
        height: auto;
      }
      &__description{
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
}