.schedule{
  &-block{
    &__header{
      display: flex;
      align-items: center;
      span{
        margin-left: 5px;
      }
      &::before{
        content: '';
        width: 36px;
        height: 36px;
        display: block;

        background: url("../img/module-arrow.svg") center center/contain no-repeat;

        margin-right: 20px;

        transform: rotate(-90deg);

        transition: .3s all;
      }
      &.active{
        &::before{
          transform: rotate(0);
        }
      }
    }
    &__title{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 700;
      font-size: 21px;

      color:$monoBlack;

      letter-spacing: -0.2px;

    }
    &__content{
      margin-top: 40px;
    }
  }
  &-grid{
    display: grid;
    grid-template-columns:minmax(0,670px);
    gap: 15px;
  }
  &-wrapper{
    &__content{
      margin-top: 45px;
    }
    &__block + &__block{
      margin-top: 45px;
    }
  }
  &-item{
    height: 100%;
    &__wrapper{
      padding: 30px;

      background: #FFFFFF;
      border-radius: 20px;

      display: flex;
      align-items: flex-start;
    }
    &__block{
      & + &{
        margin: 0 0 0 20px;
      }
    }
    &__time{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 600;
      font-size: 18px;
      line-height: 136%;

      color: #5C82D7;
    }
    &__title{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 600;
      font-size: 21px;
      line-height: 29px;
      letter-spacing: -0.2px;

      color: $monoBlack;
    }
    &__text{
      margin: 15px 0 0 0;

      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;

      color: $secondary;
    }
  }
  &-popup{
    &__list{
      &-item + &-item{
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .schedule{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,270px));
    }
  }
}

@media screen and (max-width: 600px) {
  .schedule{
    &-grid{
      grid-template-columns: minmax(0,1fr);
    }
    &-block{
      &-wrapper{
        &__content{
          margin-top: 30px;
        }
        &__block + &__block{
          margin-top: 30px;
        }
      }
    }
    &-item{
      &__title{
        font-size: 18px;
      }
    }
  }
}