.feedback{
  &-block{
    &__title{
      font-size: 17px;
      font-weight: 600;
      color:$monoBlack;

      span{
        color:$accent
      }
    }
    &__note{
      font-size: 16px;
      color:$secondary;

      margin-top: 15px;
    }
    &__content{
      margin-top: 15px;
    }
    &__list{
      list-style: none;
      &-item + &-item{
        margin-top: 15px;
      }
    }
    &__textarea{
      width: 100%;
      height: 250px;

      padding: 16px;

      background: #FFFFFF;
      border: 1px solid #D2D2D2;
      border-radius: 5px;

      resize: none;
      font-size: 16px;
      outline: none;
      &::placeholder{
        color:#95929C;
      }
    }
    &__rate{
      display: grid;
      grid-template-columns: repeat(auto-fit,minmax(0,24px));
      gap: 30px;
    }
  }
  &-wrapper{
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 130px;
    }
    &__block + &__block{
      margin-top: 30px;
    }
    &__btn{
      width: 100%;
      max-width: 390px;
      height: 60px;

      font-weight: 600;
    }
  }
}

.checkbox,.radio{
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: flex-end;
  &-column{
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    .radio__label{
      margin: 10px 0 0 0;
    }
  }
  &__item{
    display: none;
  }

  &__label{
    display: block;

    margin-left: 10px;

    font-family: 'Nunito Sans', sans-serif;
    font-size: 16px;
    color: $secondary;

    transition: .3s color linear;
  }
}

.checkbox{
  &:hover{
    cursor: pointer;
    .checkbox__checkmark{
      border-color: #95929C;
    }
  }
  &__item{
    &:checked ~ .checkbox__checkmark{
      background-color: $accent;
      border-color: $accent;
      &::before{
        opacity: 1;
      }
    }
    &:checked ~ .checkbox__label{
      color:$accent;
    }
  }
  &__checkmark{
    display: block;
    width: 16px;
    height: 16px;

    border-radius: 2px;

    border: 2px solid $secondary;

    flex-shrink: 0;
    position: relative;

    transition: .3s background-color linear, .3s border-color linear;

    &::before{
      content: '';

      position: absolute;
      left: calc(50% - 4px);
      top: calc(50% - 3px);

      width: 8px;
      height: 6px;

      display: block;
      opacity: 0;

      background: url("../img/check.svg") center center/contain no-repeat;

      transition: .3s opacity linear;
    }
  }
}

.radio{
  &:hover{
    cursor: pointer;
    .radio__checkmark{
      border-color: $secondary;
    }
  }
  &__item{
    &:checked ~ .radio__checkmark{
      background-color: $accent;
      border-color: $accent;
      &::before{
        opacity: 1;
      }
    }
    &:checked ~ .radio__label{
      color:$accent;
    }
  }
  &__checkmark{
    position: relative;
    width: 18px;
    height: 18px;

    border-radius: 50%;
    display: block;

    border: 2px solid $secondary;

    transition: .3s background-color linear, .3s border-color linear;
    &::before{
      content: '';
      position: absolute;
      top: calc(50% - 4px);
      left:calc(50% - 4px);

      width: 8px;
      height: 8px;
      border-radius: 50%;

      display: block;

      background-color: #fff;
    }
  }
}

@media screen and (max-width: 991px) {
  .feedback{
    &-wrapper{
      &__grid{
        gap: 30px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .feedback{
    &-wrapper{
      &__grid{
        grid-template-columns: minmax(0,1fr);
      }
    }
  }
}