.certificate{
  &-card{
    &__wrapper{
      padding: 30px;

      max-width: 670px;
      width: 100%;


      background: #FFFFFF;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
      border-radius: 20px;
    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;
    }
    &__name{
      font-size: 21px;
      line-height: 24px;
      letter-spacing: -0.2px;
      font-weight: 600;

      color:$monoBlack;
    }
    &__content{
      margin-top: 5px;

      display: flex;
      align-items: center;
    }
    &__footer{
      margin-top: 30px;
    }
    &__btn{
      max-width: 200px;
      height: 50px;
    }
  }
  &-info{
    &__title{
      font-family: 'Montserrat', sans-serif;
      font-weight: 600;
      font-size: 30px;
      line-height: 37px;

      color: $accent;
    }
    &__date{
      margin: 15px 0 0 0;

      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 136%;
      color:$secondary;
    }
    &__footer{
      display: grid;
      grid-template-columns: repeat(auto-fit,minmax(0,270px));

      gap: 20px;

      margin: 40px 0 0 0;
    }
    &__btn{
      height: 50px;
    }
  }
  &-single{
    &__wrapper{
      background: #FFFFFF;

      box-shadow: 0px 0px 12px rgba(92, 130, 215, 0.08);
      border-radius: 15px;

      max-width: 650px;
      width: 100%;

      padding: 12px 0 25px 36px;

      position: relative;

      overflow: hidden;

      &::before{
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;

        width: 120px;
        height: 120px;
        display: block;

        background: url("../img/certificate-single-bg.png") center center/cover no-repeat;
      }
    }
    &__text{
      font-family: 'Montserrat', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: $monoBlack;
    }
    &__name{
      font-family: 'Montserrat', sans-serif;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;

      color: $monoBlack;

      position: relative;

      width: max-content;

      margin: 0 auto;
      &::before{
        content: '';
        position: absolute;
        left: 20px;
        bottom: -4px;
        width: calc(100% - 40px);
        height: 4px;
        background: #F8D47A;
      }
    }
    &__content{
      &-header{
        margin: 18px 0 0 0;
      }
      &-name{
        margin: 27px 0 0 0;
      }
      &-footer{
        margin: 40px 0 0 0;
      }
    }
    &__footer{
      margin: 50px 0 0 0;

      padding: 0 30px 0 0;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &-logo{
    &__text{
      font-family: 'Montserrat', sans-serif;
      font-weight: 600;
      font-size: 8px;
      line-height: 10px;

      color: $monoBlack;
    }
  }

  &__single{
    margin: 40px 0 0 0;
  }
}

@media screen and (max-width: 991px) {
  .certificate{
    &-info{
      &__footer{
        margin: 30px 0 0 0;
      }
    }
    &__single{
      margin: 30px 0 0 0;
    }
  }
}

@media screen and (max-width: 600px) {
  .certificate{
    &-card{
      &__content{
        align-items: flex-start;
        flex-direction: column;
      }
      &__name{
        margin: 15px 0 0 0;
      }
    }
    &-single{
      &__wrapper{
        padding: 7px 0 15px 20px;
      }
      &__text{
        font-size: 8px;
        line-height: 10px;
      }
      &__name{
        font-size: 11px;
        line-height: 14px;

        &::before{
          left: 12px;
          width: calc(100% - 24px);
        }
      }
      &__footer{
        margin: 25px 0 0 0;
        padding: 0 18px 0 0;
      }
      &__content{
        &-header{
          margin: 10px 0 0 0;
        }
        &-name{
          margin: 14px 0 0 0;
        }
        &-footer{
          margin: 22px 0 0 0;
        }
      }
    }
    &-logo{
      &__text{
        font-size: 5px;
        line-height: 6px;
      }
    }
  }
}