.course{
  &-single{
    &__grid{
      display: grid;
      grid-template-columns: auto 320px;
      gap: 90px;


      background: #FFFFFF;
      border-radius: 15px;

      overflow: hidden;

      padding: 40px;

      &-img{

      }
      &-content{
        &-block{
          & + &{
            margin-top: 30px;
          }
        }
      }
    }

    &__title{
      font-family: 'Montserrat', sans-serif;
      font-weight: 700;
      font-size: 26px;
      line-height: 32px;

      color: $monoBlack;
    }

    &__desc{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;

      color: #020203;
    }
    &__status{
      &-text{
        font-family: 'Nunito Sans', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 136%;
        color: #898D93;
      }
      &-progress{
        margin-top: 10px;
      }
      &-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }
    &__content{
      margin-top: 30px;
    }
    &__btn{
      height: 60px;
      max-width: 270px;
      &-check{
        height: 40px;
        max-width: 225px;

        border-radius: 21px;

        &::before{
          content: '';
          width: 20px;
          height: 20px;
          display: block;

          background: url("../img/clock.svg") center center/contain no-repeat;

          margin-right: 10px;
        }
      }
    }
    &__img{
      width: 100%;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;

        border-radius: 10px;
      }
    }
    &__description{
      max-width: 670px;
      ul{
        font-family: 'Nunito Sans',sans-serif;
        font-size: 16px;
        line-height: 22px;
        list-style: disc inside;
      }
      p{
        font-family: 'Nunito Sans',sans-serif;
        font-size: 16px;
        line-height: 22px;

        color:$monoBlack;
      }

      h4{
        font-family: 'Nunito Sans', sans-serif;
        font-weight: 700;
        font-size: 21px;
        line-height: 25px;
        letter-spacing: -0.2px;

        color:$monoBlack;

        margin-bottom: 30px;
      }

      p + p{
        margin-top: 30px;
      }
      p + h4{
        margin-top: 30px;
      }
      h4 + p{
        margin-top: 30px;
      }
      p + ul{
        margin-top: 30px;
      }
      ul + p{
        margin-top: 30px;
      }
      h4 + ul{
        margin-top: 30px;
      }
      ul + h4{
        margin-top: 30px;
      }
    }
    &__modules{
      &-item + &-item{
        margin-top: 40px;
      }
    }
  }
  &-info{
    &__wrapper{
      display: flex;
    }
    &__item{
      display: flex;
      & + &{
        &::before{
          content: '';
          width: 1px;
          height: 48px;
          display: block;

          margin: 0 40px;

          background: #BCC5DA;
        }
      }
    }
    &__title{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 136%;

      color: #898D93;
    }
    &__text{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 25px;
      letter-spacing: -0.2px;

      color: #1E1E22;
    }
  }
  &-module{
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &.active{
        .course-module__title{
          &::before{
            transform: rotate(0deg);
          }
        }
      }
    }
    &__title{
      font-weight: 600;
      font-size: 25px;

      color:$monoBlack;

      letter-spacing: -0.2px;

      display: flex;
      align-items: center;
      span{
        margin-left: 5px;
      }
      &::before{
        content: '';
        width: 36px;
        height: 36px;
        display: block;

        background: url("../img/module-arrow.svg") center center/contain no-repeat;

        margin-right: 20px;

        transform: rotate(-90deg);

        transition: .3s all;
      }
    }
    &__status{
      display: flex;
      flex-direction: column;
      &-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      &-progress{
        margin: 10px 0 0 0;

        width: 380px;
      }
      &-text{
        font-family: 'Nunito Sans';
        font-weight: 400;
        font-size: 14px;
        color:$secondary;
      }
    }
    &__content{
      margin-top: 40px;
    }
  }
  &-table{
    &__row{
      display: grid;
      grid-template-columns: 20px 145px auto;
      gap: 15px;

      width: 100%;
      max-width: 600px;
    }
    &__row + &__row{
      margin-top: 15px;
    }
    &__column{
      display: flex;
      align-items: center;
      &-progress{
        padding-left: 20px;
      }
    }
    &__count{
      width: 30px;

      text-align: right;

      flex-shrink: 0;
    }
    &__progress{
      width: 100%;
      margin-left: 40px;
      &-bar{
        width: 100%;
        max-width: 315px;
        height: 10px;
        border-radius: 0;

        margin-left:auto;
      }
    }
    &__title{
      font-size: 16px;
      color:#95929C;
    }
    &__text{
      font-size: 16px;
      color:#020203;
    }
  }
  &-review{
    position: fixed;
    bottom: 0;
    z-index: 99;
    width: 100%;
    &__wrapper{
      height: 70px;

      background: #FFFFFF;
      display: flex;
      align-items: center;
    }
    &__content{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__mark{
      display: flex;
      align-items: center;
      &-title{
        font-size: 16px;
        color: #95929C;
      }
      &-content{
        margin-left: 15px;
      }
    }
    &__input{
      margin-left: 45px;
      &-item{
        background: #FFFFFF;
        border: 1px solid #D2D2D2;
        border-radius: 5px;

        width: 370px;
        height: 40px;

        font-size: 16px;
        padding: 10px 16px;

        outline: none;

        &::placeholder{
          color: #95929C;
        }
      }
    }
    &__submit{
      margin-left: 15px;
      &-btn{
        width: 175px;
        height: 40px;
      }
    }
  }
}

.lesson{
  &-grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 275px;
    gap: 40px 30px;
    &__item{
      height: 100%;
    }
  }
  &-item{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{
      .lesson-item__desc{
        color:$accent;
      }
    }
    &_disabled{
      opacity: .5;
      pointer-events: none;
      .lesson{
        &-item{
          &__progress{
            display: none;
          }
          &__date{
            display: flex;
          }
        }
      }
    }
    &__header{
      &-info{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }
    &__balls{
      font-size: 16px;
      color:$accent;
    }
    &__wrapper{
      height: 100%;
      padding: 20px;

      background: #FFFFFF;
      border-radius: 25px;


      display: flex;
      flex-direction: column;
    }
    &__title{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;

      color: #898D93;
    }
    &__status{
      margin-bottom: 8px;

      font-family: 'Nunito Sans', sans-serif;
      font-style: normal;
      font-weight: 400;
      width: max-content;
      font-size: 16px;
      &-completed{
        color:$secondary;
      }
      &-failed{
        color:$accent;
      }
    }
    &__content{
      margin-top: 15px;
    }
    &__desc{
      font-family: 'Nunito Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;
      color: #1E1E22;

      transition: .5s color;
    }
    &__footer{
      padding-top: 35px;
      margin-top: auto;

      width: 100%;
    }
    &__progress{
      width: 100%;
    }
    &__date{
      display: none;

      align-items: center;
      justify-content: space-between;
      &::after{
        content: '';
        width: 24px;
        height: 24px;
        display: block;

        background: url("../img/lock.svg") center center/contain no-repeat;
      }
      &-text{
        font-family: 'Nunito Sans', sans-serif;
        font-weight: 400;
        font-size: 16px;
        color:$secondary;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .course{
    &-single{
      &__grid{
        grid-template-columns: minmax(0,1fr);

        gap: 30px;
        &-content{
          order: 2;
        }
      }
      &__img{
        order: 1;

        height: 370px;
      }

    }
    &-module{
      &__header{
        flex-direction: column;
        align-items: flex-start;
      }
      &__status{
        margin-top: 30px;
      }
    }
    &-review{
      &__input{
        margin-left: 15px;
      }
    }
  }
  .lesson-grid{
    grid-template-columns: repeat(2,minmax(0,270px));
  }
}

@media screen and (max-width: 991px) {
  .course{
    &-review{
      &__wrapper{
        height: auto;

        padding: 15px 0;
      }
      &__content{
        flex-wrap: wrap;
      }
      &__mark,&__input,&__submit{
        margin: 5px 10px;
      }
      &__input{
        &-item{
          width: 100%;
        }
      }
    }
  }
}


@media screen and (max-width: 767px) {
  .course{
    &-info{
      &__wrapper{
        flex-direction: column;
      }
      &__item{
        & + &{
          margin: 30px 0 0 0;
          &::before{
            display: none;
          }
        }
      }
    }
    &-module{
      &__title{
        flex-wrap: wrap;
        span{
          width: 100%;
          padding-left: 56px;
        }
      }
    }
  }
}


@media screen and (max-width: 600px) {
  .lesson{
    &-grid{
      grid-template-columns: minmax(0,1fr);
      grid-auto-rows: auto;
      gap: 30px;
    }
  }
  .course{
    &-module{
      &__status{
        width: 100%;
      }
    }
    &-table{
      &__row{
        grid-template-columns: 20px auto 50px;
      }
      &__progress{
        display: none;
      }
    }
  }
}